.howe-it-background {
  background-repeat: no-repeat;
  background-color: $pink-background-color;

  @media screen and (max-width: 767px) {
    background-image: url(../images/how-it/how-it-fon-mob@2x.png),
      url(../images/how-it/how-it-fon-mob.png);

    @media screen and (min-width: 320px) {
      background-position: 100% 48%;
      background-size: 100%;

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url(../images/how-it/how-it-fon-desk@2x.png),
          url(../images/how-it/how-it-fon-desk.png);
      }
    }
  }
}
.how-it-background-image {
  @media screen and (min-width: 1200px) {
    background-image: url(../images/how-it/how-it-fon-desk.png);
    background-position: 100% 53%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    background-repeat: no-repeat;

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(../images/how-it/how-it-fon-desk.png);
    }
  }
}

/*MOB*/
@media screen and (max-width: 767px) {
  .hawe-its-made {
    padding-top: 125px;
    padding-bottom: 145px;
  }

  .hawe-it-main-taitle h2 {
    font-family: Titan One;
    width: 200px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 26px;
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $primary-title-color;
  }

  .hawe-it-main-taitle p {
    font-family: Titan One;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17px;
    margin-left: auto;
    margin-right: auto;
    text-transform: uppercase;
    margin-bottom: 15px;
    text-align: center;
    color: #ffa5ba;
  }
  .hawe-it-container-about-item img {
    max-width: 100%;
    height: auto;
    overflow: auto;
    margin-bottom: 46px;
  }

  .hawe-it-text-black {
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.76;
    margin-bottom: 33px;
    color: $primary-text-color;
    font-size: 14px;
    line-height: 1.76;
  }

  .hawe-it-text-about {
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.5;
    margin-bottom: 17px;
    margin-right: auto;
    margin-left: auto;
    color: #907e82;
  }

  .hawe-it-text-about-two {
    margin-bottom: 62px;
  }

  .hawe-its-made-btn {
    width: 176px;
    height: 44px;
    background: #ffffff;
    border-radius: 22px;
    margin-bottom: 39px;
    margin-left: auto;
    margin-right: auto;
    padding-right: 37px;
    padding-left: 37px;
    justify-content: center;
    border: none;
    font-family: DM Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.29px;
    display: flex;
    align-items: center;
    text-align: center;
    color: $primary-title-color;
    &:focus,
    &:hover {
      background-color: #ecebeb;
      border: none;
    }
    svg {
      margin-left: 14px;
      width: 5px;
      height: 8px;
    }
  }

  .hawe-it-container-our-advantages-item img {
    margin-top: 20px;
    margin-bottom: 16px;
  }
  .hawe-it-container-our-advantages-item h3 {
    width: 98px;
    height: 56px;
    left: 20px;
    top: 3716px;

    font-family: Titan One;
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 41px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #d41443;
  }

  .hawe-it-container-our-advantages-item p {
    display: inline-block;
    width: 230px;
    height: 36px;
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    color: $primary-text-color;
  }
}
